.card {
  display: flex;
  flex-direction: row;
  height: auto;
  margin: auto auto 25px auto;
  width: 80%;
}

.card img {
  transition: 0.5s ease-in-out;
  height: auto;
  max-height: 100%;
  height: 300px;
  width: 400px;
}
.card-body {
  text-align: right;
  padding: 40px 100px 40px 15px;
  height: 300px;
}
.card-toolbar {
  border-bottom: 1px solid lightgrey;
}
.card-text-wrapper {
  text-align: right;
  height: 150px;
  padding-left: 50px;
}
.card-inner-body {
  padding-top: 15px;
}
.card-text {
  opacity: 0.8;
  text-overflow: ellipsis;
  overflow: hidden;
  word-break: break-word;
  height: 100px;
  direction: rtl;
}
.card-bottom {
  border-top: 1px solid lightgrey;
  overflow: hidden;
  height: 25px;
}
.card-toolbar {
  font-size: 0.75rem;
}

@media (max-width: 1024px) {
  .card {
    flex-direction: column;
    width: 100%;
  }
  .card-body {
    text-align: right;
    width: 100%;
    padding: 5px 5px 5px 5px;
  }
  .card img {
    height: auto;
    max-height: 100%;
    max-width: 100%;
  }
}
